@import '../../globalVariable';

.updateSuccess {
    margin: 0 30px;
    .figure {
        width: 192px;
        height: 160px;
        margin: 40px auto 30px;
        background: url('../../assets/updateSuccess.png');
        background-size: contain;
        @media screen and (-webkit-min-device-pixel-ratio: 2) {
            background: url('../../assets/updateSuccess@2x.png');
            background-size: contain;
        }
    }
    .desc {
        font-size:20px;
        text-align: center;
    }

    .confirmBtn {
        margin-top: 40px;
    }

}

